.kotak_login {
  width: 500px;
  background: white;
  position: relative;
  margin: 80px auto;
  padding: 30px 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}


label {
  font-size: 11pt;
  justify-content: left;
}

.form_login {
  /* Makes the form input take up the full width of its container */
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  font-size: 11pt;
  margin-bottom: 20px;

  /* Define the default styles for the input */
  background-color: #fff;
  border: 1px solid #ccc;
  color: #828080;
  border-radius: 0px;
}

.powered {
  font-size: 11pt; color: #898989;


}

.form_login:hover {
  /* Define the styles for the input when hovered */
  background-color: #f0f0f0;
  border: 1px solid #999;
  color: #a7a7a7;
}


.tombol_login {
  background: #13b3e9;
  color: #fff; 
  font-size: 11pt;
  width: 100%;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.tombol_login:hover {
  background: #4ab8e3; /* Change the background color on hover */
  color: #fff; /* Change the text color on hover */
}


.tombol_logout {
 background: #13b3e9;
  color: #fff; 
  font-size: 11pt;
  width: 10%;
  border: none;
  border-radius: 10px;
  padding: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.tombol_logout:hover {
  background: #4ab8e3; /* Change the background color on hover */
  color: #fff; /* Change the text color on hover */
}

