  animation: blink 2s ease infinite;
}


.box_status_error {

  background-color: #e83f3f;
  color: white;
  padding: 2px;
  border-radius: 4px;
  width: 100%;
  text-align: center; height: 30px; font-size: 17px;
  /* Your existing styles */

  /* Add animation properties for blinking */
  animation: blink 2s ease infinite;
}


/* Define the animation keyframes */
@keyframes blink {
  0%, 100% {
    opacity: 1; /* Fully visible */
  }
  50% {
    opacity: 0.5; /* Semi-transparent */
  }
}
.auto-resize-wrapper {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 14px; 
  background-color: white; 
  width: 80%; margin: auto;
  border-radius: 5px;
  margin-top: 100px; text-align: left;
}
.mybg {

  height: 100%; /* Ensure the image does not exceed its parent's height */

background-color: #def5ff;

}

.custom-file-upload {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007BFF; /* Set your desired button color */
  color: white; /* Text color */
  border: 1px solid #007BFF; /* Border color */
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Add a transition for a smoother hover effect */
}

.custom-file-upload:hover {
  background-color: #0056b3; /* Change the background color on hover */
}

.input {

width: 100%; height: 40px;
font-size: larger;

}

.custom-button {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  background-color: #3498db;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
}

.custom-button:hover {
  background-color: #2980b9;
}

.custom-button.clicked {
  background-color: #2ecc71;
}
